
body{
  padding:30px 0 0;
  margin:0;
}
section {
  min-height: 700px;
  margin-bottom: 300px;
  transition-delay: 0.5s;
}
#navbar
{
    position: fixed;
    top: 0px;
    display: flex;
    /*justify-content: space-between;
    flex-direction: column; */
    width: 100%;
    height: 10%;
    transition: 0.09s;
    background-color:#007EFF;
    color:white;
    border-bottom: 0.5px solid;
    border-radius: 0px 0px 20px 20px;
    padding: 1% 2% 0% 1%;
    z-index: 5;
   
}

#logo{
  height:50px;
  width: 150px; 
  justify-content: flex-start;
}
@font-face {
  font-family: FatFrank;
  src: url(./components/assets/fonts/FatFrank/FatFrank-Regular.otf);
}
@font-face {
  font-family: Roboto;
  src: url(./components/assets/fonts/Roboto/Roboto-Bold.ttf);
}



#navlinks
{
    transition: 0.2s;
    position:fixed;
    right:10px;
    top:20px;
    
    }

    a:hover, a:visited, a:link, a:active
{
    text-decoration: none;
    font-weight: bold;
    color:white;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size:30px;
}

.navlink{
  padding-right:15px
}


#pageOne{
      /* position: relative;
      left:-10%; */
      text-align:left;
      color:#007EFF;
      margin:auto;
      font-family:FatFrank;
      font-size: 5em;
      height:1000px;
      width:100%;
      display:flex;
      justify-content:space-between;
}


#pageTwo{
      text-align:center;
      font-family:FatFrank;
      font-size: auto;
      height:1000px;
      display:flex;
      justify-content:space-between;
}

